@font-face {
  font-family: iconfont;
  src: url("../../assets/fonts/iconfont.eot?t=1576392666910"); /* IE9 */
  src: url("../../assets/fonts/iconfont.eot?t=1576392666910#iefix") format("embedded.opentype"), /* IE6.IE8 */
       url("../../assets/fonts/iconfont.woff2?t=1576392666910") format("woff2"),
       url("../../assets/fonts/iconfont.woff?t=1576392666910") format("woff"),
       url("../../assets/fonts/iconfont.ttf?t=1576392666910") format("truetype"),  /* chrome, firefox, opera, Safari, Android, iOS 4.2+ */
       url("../../assets/fonts/iconfont.svg?t=1576392666910#iconfont") format("svg"); /* iOS 4.1- */
}

.icon {
  display: inline-block;
  opacity: 1;
  font-family: "iconfont" !important;
  font-size: 1em;
  font-style: normal;
  font-variant: normal;
  text-decoration: inherit;
  text-transform: none;
  text-align: center;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  backface-visibility: hidden;
}

.icon:before {
  background: none !important;
}

.icon.loading {
  height: 1em;
  line-height: 1;
  animation: icon-loading 2s linear infinite;
}

@keyframes icon-loading {
  0% {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(1turn);
  }
}

