.mde-ui.buttons {
  display: inline-flex;
  flex-direction: row;
  vertical-align: baseline;
  margin: 0 0.25em 0 0;
  box-shadow: none;
  font-size: 1em;
}
.mde-ui.buttons:after {
  content: '.';
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
  font-size: 0;
}
.mde-ui.buttons .icon-button:not(.separator) {
  flex: 1 0 auto;
  border-radius: 0;
  margin: 0;
}
.mde-ui.buttons .icon-button:first-child {
  border-left: none;
  margin-left: 0;
  border-top-left-radius: 0.28571429em;
  border-bottom-left-radius: 0.28571429em;
}
.mde-ui.buttons .icon-button:first-child div:first-child {
  border-top-left-radius: 0.28571429em;
  border-bottom-left-radius: 0.28571429em;
}
.mde-ui.buttons .icon-button:last-child {
  border-top-right-radius: 0.28571429em;
  border-bottom-right-radius: 0.28571429em;
}
.mde-ui.buttons .icon-button:last-child div:last-child {
  border-top-right-radius: 0.28571429em;
  border-bottom-right-radius: 0.28571429em;
}
