
.mde-ui.splitpane {
  display: flex;
  flex: 1;
  position: absolute;
  outline: none;
  user-select: text;

  &.horizontal {
    width: 100%;
    flex-direction: column;
    top: 0;
    bottom: 0;
  }

  &.vertical {
    height: 100%;
    flex-direction: row;
    left: 0;
    right: 0;
  }
}

.mde-ui.pane {
  overflow: hidden;
  position: relative;
  outline: none;

  &.pane1 {
    flex: none;
  }

  &.pane2 {
    flex: 1;
  }
}

.mde-ui.resizer {
  background: #464646;
  opacity: 0.2;
  z-index: 200;
  box-sizing: border-box;
  background-clip: padding-box;

  &:hover {
    transition: all 2s ease;
  }

  &.horizontal {
    height: 11px;
    margin: -5px 0;
    border-top: 5px solid hsla(0, 0%, 100%, 0);
    border-bottom: 5px solid hsla(0, 0%, 100%, 0);
    cursor: row-resize;
    width: 100%;

    &:hover {
      border-top: 5px solid rgba(0, 0, 0, 0.5);
      border-bottom: 5px solid rgba(0, 0, 0, 0.5);
    }
  }

  &.vertical {
    width: 11px;
    margin: 0 -5px;
    border-left: 5px solid hsla(0, 0%, 100%, 0);
    border-right: 5px solid hsla(0, 0%, 100%, 0);
    cursor: col-resize;

    &:hover {
      border-left: 5px solid rgba(0, 0, 0, 0.5);
      border-right: 5px solid rgba(0, 0, 0, 0.5);
    }
  }

  &.disabled {
    cursor: not-allowed;

    &:hover {
      border-color: transparent;
    }
  }
}
