.mde-ui.dropdown {
  display: inline-flex;
  flex-direction: column;
  vertical-align: baseline;
  margin: 0 0.28571429em 0 0;
  box-shadow: none;
  font-size: 1em;
  outline: 0;
}
.mde-ui.dropdown .icon-button:not(.separator) {
  text-align: left;
  white-space: nowrap;
}
.mde-ui.dropdown .icon-button:not(.separator).menubutton {
  border-radius: 0.28571429em;
}
.mde-ui.dropdown .icon-button:not(.separator).menubutton .content-right {
  padding-right: 0.28571429em;
  border-top-right-radius: 0.28571429em;
  border-bottom-right-radius: 0.28571429em;
}
.mde-ui.dropdown .icon-button:not(.separator).menubutton .content-left {
  border-top-left-radius: 0.28571429em;
  border-bottom-left-radius: 0.28571429em;
}
.mde-ui.buttons.dropdown {
  flex-direction: column;
  position: absolute;
  top: 40px;
  z-index: 999;
  float: left;
  min-width: 12em;
  margin: 0;
  padding: 5px 0;
  text-align: left;
  outline: 0;
  background: #e0e1e2 none;
  background-clip: padding-box;
  border: 1px solid #ccc;
  border: 1px solid rgba(0,0,0,0.15);
  border-radius: 0.28571429em;
  box-shadow: 0 6px 12px rgba(0,0,0,0.175);
}
.mde-ui.buttons.dropdown .icon-button:first-child,
.mde-ui.buttons.dropdown .icon-button:last-child,
.mde-ui.buttons.dropdown .icon-button:first-child div:first-child,
