.mde-ui.splitpane {
  display: flex;
  flex: 1;
  position: absolute;
  outline: none;
  user-select: text;
}
.mde-ui.splitpane.horizontal {
  width: 100%;
  flex-direction: column;
  top: 0;
  bottom: 0;
}
.mde-ui.splitpane.vertical {
  height: 100%;
  flex-direction: row;
  left: 0;
  right: 0;
}
.mde-ui.pane {
  overflow: hidden;
  position: relative;
  outline: none;
}
.mde-ui.pane.pane1 {
  flex: none;
}
.mde-ui.pane.pane2 {
  flex: 1;
}
.mde-ui.resizer {
  background: #464646;
  opacity: 0.2;
  z-index: 200;
  box-sizing: border-box;
  background-clip: padding-box;
}
.mde-ui.resizer:hover {
  transition: all 2s ease;
}
.mde-ui.resizer.horizontal {
  height: 11px;
  margin: -5px 0;
  border-top: 5px solid rgba(255,255,255,0);
  border-bottom: 5px solid rgba(255,255,255,0);
  cursor: row-resize;
  width: 100%;
}
.mde-ui.resizer.horizontal:hover {
  border-top: 5px solid rgba(0,0,0,0.5);
  border-bottom: 5px solid rgba(0,0,0,0.5);
}
.mde-ui.resizer.vertical {
  width: 11px;
  margin: 0 -5px;
  border-left: 5px solid rgba(255,255,255,0);
  border-right: 5px solid rgba(255,255,255,0);
  cursor: col-resize;
}
.mde-ui.resizer.vertical:hover {
  border-left: 5px solid rgba(0,0,0,0.5);
  border-right: 5px solid rgba(0,0,0,0.5);
}
.mde-ui.resizer.disabled {
  cursor: not-allowed;
}
.mde-ui.resizer.disabled:hover {
  border-color: transparent;
}
