
.mde-workspace-container {
  position: relative;
  flex: 1;
  outline: none;
  min-width: 50em;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;

  .mde-toolbar {
    position: relative;
    border-bottom: 1px solid #ddd;
    display: inline-flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    z-index: 201;

    .mde-toolbar-left, .mde-toolbat-right {
      display: inherit;
    }

    .mde-toolbar-actions {
      padding: 4px 5px;
      min-height: 34px;
      display: inline-flex;
      flex-direction: row;
      flex-wrap: nowrap;

      &.nav {
        min-width: 40px;
        padding-left: 0;

        .mde-ui.dropdown {
          &.mainmenu {
            background: url('../assets/img/logo.png') no-repeat;
            background-size: contain;

            .icon-button.menubutton {
              background-color: unset;
            }
          }

          .icon-button {
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
          }
        }

        .mde-ui.buttons {
          margin-right: 1em;
        }
      }
    }
  }

  .mde-editor-container {
    position: relative;
    flex: 1;

    .raw-editor {
      min-width: 25em;
    }

    .mde-scroll-container {
      overflow: hidden;
      width: 100%;
      height: 100%;

      .mdse-preview {
        width: auto;
        height: 100%;
        overflow-y: auto;
      }
    }

    ::-webkit-scrollbar {
      -webkit-appearance: none;
      width: 10px;
      height: 10px;
    }

    ::-webkit-scrollbar-track {
      background: rgb(241, 241, 241);
      border-radius: 0;
    }

    ::-webkit-scrollbar-thumb {
      cursor: pointer;
      border-radius: 5px;
      background: rgba(0, 0, 0, 0.25);
      transition: color 0.2s ease;
    }

    ::-webkit-scrollbar-thumb:window-inactive {
      background: rgba(0, 0, 0, 0.15);
    }

    ::-webkit-scrollbar-thumb:hover {
      background: rgba(128, 135, 139, 0.8);
    }
  }
}
