@import '../../assets/css/tooltip.css';
@import '../../assets/css/iconfont.css';
.mde-ui.icon-button {
  cursor: pointer;
  display: inline-flex;
  position: relative;
  flex: 1 0 auto;
  min-height: 1em;
  outline: none;
  border: none;
  vertical-align: baseline;
  background: #e0e1e2 none;
  color: rgba(0,0,0,0.6);
  text-transform: none;
  text-shadow: none;
  font-size: 1em;
  font-weight: 400;
  line-height: 1em;
  font-style: normal;
  text-align: center;
  text-decoration: none;
  user-select: none;
  transition: opacity 0.1s ease, background-color 0.1s ease, color 0.1s ease, box-shadow 0.1s ease, background 0.1s ease;
  will-change: '';
  -webkit-tap-highlight-color: transparent;
}
.mde-ui.icon-button span[data-tooltip] {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 1000;
}
.mde-ui.icon-button .content-left {
  display: inherit;
  padding: 0.618em;
}
.mde-ui.icon-button .content-right {
  display: inherit;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0.618em 0.618em 0.618em 0;
}
.mde-ui.icon-button.separator {
  cursor: default;
  opacity: 0.2 !important;
  background-image: none !important;
  box-shadow: none !important;
  pointer-events: none !important;
  border: 2px solid rgba(255,255,255,0);
  min-height: 5px;
  min-width: 5px;
  background-color: #464646;
  background-clip: padding-box;
}
.mde-ui.icon-button:disabled,
.mde-ui.icon-button.disabled,
.mde-ui.icon-button.disabled:hover {
  cursor: default;
  opacity: 0.45 !important;
  background-image: none !important;
  box-shadow: none !important;
  pointer-events: none !important;
}
.mde-ui.icon-button:selected,
.mde-ui.icon-button.selected,
.mde-ui.icon-button.selected:hover {
  cursor: default;
  box-shadow: none !important;
  pointer-events: none !important;
}
.mde-ui.icon-button,
.mde-ui.icon-button:hover {
  box-shadow: inset 0 0 0 1px transparent, inset 0 0 0 0 rgba(34,36,38,0.15);
}
.mde-ui.icon-button:hover {
  background-color: #cacbcd;
  background-image: none;
  color: rgba(0,0,0,0.8);
}
.mde-ui.icon-button:hover .icon {
  opacity: 0.85;
}
.mde-ui.icon-button:focus {
  background-color: #cacbcd;
  color: rgba(0,0,0,0.8);
  background-image: '' !important;
  box-shadow: '' !important;
}
.mde-ui.icon-button:focus .icon {
  opacity: 0.85;
}
.mde-ui.icon-button:active {
  background-color: #babbbc;
  background-image: '';
  color: rgba(0,0,0,0.9);
  box-shadow: inset 0 0 0 1px transparent, none;
}
.content-left > .icon {
  margin: auto !important;
  vertical-align: top;
  width: 1em;
  transition: opacity 0.1s ease;
}
.content-right > .shortkey {
  padding-left: 16px;
}
.content-right > .caret {
  display: inline-block;
  width: 0;
  height: 0;
  vertical-align: middle;
  border-top: 4px dashed;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
}
