.mde-workspace-container {
  position: relative;
  flex: 1;
  outline: none;
  min-width: 50em;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
}
.mde-workspace-container .mde-toolbar {
  position: relative;
  border-bottom: 1px solid #ddd;
  display: inline-flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  z-index: 201;
}
.mde-workspace-container .mde-toolbar .mde-toolbar-left,
.mde-workspace-container .mde-toolbar .mde-toolbat-right {
  display: inherit;
}
.mde-workspace-container .mde-toolbar .mde-toolbar-actions {
  padding: 4px 5px;
  min-height: 34px;
  display: inline-flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.mde-workspace-container .mde-toolbar .mde-toolbar-actions.nav {
  min-width: 40px;
  padding-left: 0;
}
.mde-workspace-container .mde-toolbar .mde-toolbar-actions.nav .mde-ui.dropdown.mainmenu {
  background: url("../assets/img/logo.png") no-repeat;
  background-size: contain;
}
.mde-workspace-container .mde-toolbar .mde-toolbar-actions.nav .mde-ui.dropdown.mainmenu .icon-button.menubutton {
  background-color: unset;
}
.mde-workspace-container .mde-toolbar .mde-toolbar-actions.nav .mde-ui.dropdown .icon-button {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.mde-workspace-container .mde-toolbar .mde-toolbar-actions.nav .mde-ui.buttons {
  margin-right: 1em;
}
.mde-workspace-container .mde-editor-container {
  position: relative;
  flex: 1;
}
.mde-workspace-container .mde-editor-container .raw-editor {
  min-width: 25em;
}
.mde-workspace-container .mde-editor-container .mde-scroll-container {
  overflow: hidden;
  width: 100%;
  height: 100%;
}
.mde-workspace-container .mde-editor-container .mde-scroll-container .mdse-preview {
  width: auto;
  height: 100%;
  overflow-y: auto;
}
.mde-workspace-container .mde-editor-container ::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 10px;
  height: 10px;
}
.mde-workspace-container .mde-editor-container ::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 0;
}
.mde-workspace-container .mde-editor-container ::-webkit-scrollbar-thumb {
  cursor: pointer;
  border-radius: 5px;
  background: rgba(0,0,0,0.25);
  transition: color 0.2s ease;
}
.mde-workspace-container .mde-editor-container ::-webkit-scrollbar-thumb:window-inactive {
  background: rgba(0,0,0,0.15);
}
.mde-workspace-container .mde-editor-container ::-webkit-scrollbar-thumb:hover {
  background: rgba(128,135,139,0.8);
}
